import $ from 'blingblingjs';

const [wrapper] = $('.js-search-inline-wrapper');
const searchToggles = $('.js-search-inline-toggle');
const cssKeys = {
    active: 'd-none',
};

const closeToggle = $('.js-search-inline-close-toggle');

const handleClickOnTogglle = (e) => {
    // stop default action
    e.preventDefault();
    // show/hide search
    wrapper.classList.toggle(cssKeys.active);
    // place focus on input
    const [input] = $('input[type=search', wrapper);
    input.focus();
};

const close = () => {
    wrapper.classList.add(cssKeys.active);
};

searchToggles.on('click', handleClickOnTogglle);

if (closeToggle) closeToggle.on('click', close);
